.menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    // border: 1px solid black;  
    margin: 0 7.5px 15px;
    background-position: center;
    background-size: cover;
    &:hover{
      transition: all ease-in-out 0.4s;
      transform: scale(1.01);
    }
    &.large{
      height: 280px;
    }
    &:first-child {
      margin-right: 7.5px;
    }
  
    &:last-child {
      margin-left: 7.5px;
    }
  
    .content {
      
      height: 80px;
      padding: 0 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: rgba(174, 174, 174, 0.592);
      // border: 1px solid black;
  
      .title {
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: #ededed;
      }
  
      .subtitle {
        font-weight: lighter;
        font-size: 16px;
      }
    }
  }